import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { faAngleDown, faCheckCircle, faCloudMoon, faTimesCircle, faSyncAlt, faCat, faDog } from "@fortawesome/free-solid-svg-icons";
import DOMPurify from "dompurify";

import { arachne } from "./arachne.js";

class Status extends React.Component{
    constructor(props){
        super(props);
        this.state = {id: this.props.status.id, visible: false}
        this.statusBox = React.createRef();
        this.timeOutHandle = null;
    }
    render(){
        if(this.state.visible){
            let style={
                position: "fixed",
                bottom: "30px",
                left: "30px",
                backgroundColor: "white",
                boxShadow: "0 2px 5px #d9d9d9",
                transition: "opacity 0.5s",
                opacity: "1",
                borderRadius: "3px",
                overflow: "hidden",
                zIndex: 9000000
            };
            let statusTxt = "";
            let statusSymbol = null;
            switch(this.state.type){
                case "searching":
                    statusSymbol = <div style={{display: "inline-block", backgroundColor:"#246EB9"}}><FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", fontSize:"40px"}} icon={faSyncAlt} spin /></div>;
                    statusTxt = "Suche läuft...";
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    break;
                case "found":
                    statusSymbol = <FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", backgroundColor:"#4CB944", fontSize:"40px"}} icon={faDog} />;
                    statusTxt = "Einträge gefunden!";
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    this.timeOutHandle = setTimeout(()=>{
                        this.statusBox.current.style.opacity = "0";
                        setTimeout(()=>{this.setState({visible: false})}, 500);
                    }, 3000);
                    break;
                case "notFound":
                    statusSymbol = <FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", backgroundColor:"#353535", fontSize:"40px"}} icon={faCat} />;
                    statusTxt = "Keine Einträge gefunden!";
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    this.timeOutHandle = setTimeout(()=>{
                        this.statusBox.current.style.opacity = "0";
                        setTimeout(()=>{this.setState({visible: false})}, 500);
                    }, 3000);
                    break;
                case "saved":
                    statusSymbol = <FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", backgroundColor:"#4CB944", fontSize:"40px"}} icon={faCheckCircle} />;
                    statusTxt = "Speichern erfolgreich.";
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    this.timeOutHandle = setTimeout(()=>{
                        this.statusBox.current.style.opacity = "0";
                        setTimeout(()=>{this.setState({visible: false})}, 500);
                    }, 2000);
                    break;
                case "error":
                    statusSymbol = <FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", backgroundColor:"#F06543", fontSize:"40px"}} icon={faTimesCircle} />;
                    statusTxt = "Ein Fehler ist aufgetreten.";
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    this.timeOutHandle = setTimeout(()=>{
                        this.statusBox.current.style.opacity = "0";
                        setTimeout(()=>{this.setState({visible: false})}, 500);
                    }, 3000);
                    break;
                case "saving":
                    statusTxt = "Einträge werden gespeichert.";
                    statusSymbol = <div style={{display: "inline-block", backgroundColor:"#246EB9"}}><FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", fontSize:"40px"}} icon={faSyncAlt} spin /></div>;
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    this.timeOutHandle = null;
                    break;
                default:
                    statusTxt = "Programm arbeitet.";
                    statusSymbol = <FontAwesomeIcon color="#FDFFFC" style={{padding: "3px", backgroundColor:"#F5EE9E", fontSize:"40px"}} icon={faCloudMoon} />;
                    if(this.timeOutHandle!=null){clearTimeout(this.timeOutHandle)};
                    this.timeOutHandle = setTimeout(()=>{
                        this.statusBox.current.style.opacity = "0";
                        setTimeout(()=>{this.setState({visible: false})}, 500);
                    }, 2000);
            }
            if(this.state.value!=null){statusTxt=this.state.value}
            return <div ref={this.statusBox} style={style}>{statusSymbol}<span style={{position:"relative", top: "-10px", padding: "10px 20px"}}>{statusTxt}</span></div>;
        } else {
            return null;
        }
    }
    componentDidUpdate(){
        if(this.props.status.id!=this.state.id){
            this.setState({id: this.props.status.id, visible: true, type: this.props.status.type, value: this.props.status.value});
        }
    }
}

class SelectMenu extends React.Component{
    render(){
        let options = [];
        for(const op of this.props.options){
            options.push(<option key={op[0]} value={op[0]}>{op[1]}</option>)
        }
        return (
        <div style={this.props.style}>
            <select value={this.props.value} style={{width:"100%"}} onChange={event=>{this.props.onChange(event)}}>
                {options}
            </select>
            <div style={{pointerEvents:"none", textAlign:"right", margin: "-26px 10px 0 0"}}><FontAwesomeIcon color="LightGray" icon={faAngleDown} /></div>
        </div>);
    }
    componentDidUpdate(){}
}

class AutoComplete extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            defaultValue: this.props.value,
            cInput: this.props.value,
            items: [],
            currentItemId: null
        };
    }
    render(){
        let itemLst = [];
        let style = {
            position: "absolute",
            backgroundColor: "white",
            boxShadow: "0 1px 5px #d9d9d9",
            padding: "10px 15px",
            zIndex: 1000000
        };
        let inputStyle = {};
        if(this.state.items.length===1&&this.state.items[0][this.props.col]===this.state.cInput){
            inputStyle = {color: "#2364AA"};
            style.display = "none";
        } else if(this.state.items.length>0){
            for(const item of this.state.items){
                let itemStyle = {};
                if(this.state.currentItemId===item.id){itemStyle.color="#2364AA"}
                itemLst.push(<div key={item.id} onClick={e=>{this.selectItemClick(e.target.textContent)}} style={itemStyle}>{item[this.props.col]}</div>);

            }
        } else {style.display = "none"}
        return (
        <div>
            <input type="text" style={inputStyle} onKeyDown={e=>{this.selectItem(e.keyCode)}} onBlur={()=>{setTimeout(()=>{this.leave()},100)}} onChange={e=>{this.change(e.target.value)}} value={this.state.cInput} />
            <div style={style}>{itemLst}</div>
        </div>);
    }
    selectItemClick(value){this.change(value)}
    selectItem(keyCode){
        if(keyCode===38&&this.state.currentItemIndex>0){
            this.setState({currentItemIndex: this.state.currentItemIndex-1, currentItemId: this.state.items[this.state.currentItemIndex-1].id});
        } else if (keyCode===40&&this.state.currentItemIndex<this.state.items.length){
            this.setState({currentItemIndex: this.state.currentItemIndex+1, currentItemId: this.state.items[this.state.currentItemIndex+1].id});
        } else if (keyCode===13){
            this.change(this.state.items[this.state.currentItemIndex][this.props.col]);
        }
    }
    leave(){if(this.state.items.length>1){this.setState({items: []})}}
    change(input){
        if(input!==""&&input!==this.state.cInput){
            let search = {};
            search[this.props.col] = input+"*";
            arachne[this.props.tbl].get(search, {select: ["id", this.props.col], limit:10, order: [this.props.col]})
            .then(re=>{
                if(re.length>0){
                    this.props.onChange(((re.length===1&&re[0][this.props.col]===input)?re[0].id:null),input);
                    this.setState({cInput: input, items: re, currentItemIndex: 0, currentItemId: re[0].id});
                } else {
                    this.props.onChange(null,input);
                    this.setState({cInput: input, items: []});
                }
            });
        } else if (input===""){
            this.props.onChange(null,input);
            this.setState({cInput: "", items: []});
        }
    }
    componentDidUpdate(){
        if(this.state.defaultValue!==this.props.value){this.setState({defaultValue: this.props.value, cInput: this.props.value})}
    }
}

class SelectorWrapper extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return <div className="selectWrapper" style={this.props.style} id={this.props.children.props.id} ref={this.element} onClick={event=>{event.stopPropagation();/*console.log(this.props.children.props)*/}} onMouseUp={event=>{this.props.onSelect(this.props.children, {shift: event.shiftKey, meta: event.metaKey, ctrl: event.ctrlKey})}}>{this.props.children}</div>;
    }
}

class Selector  extends React.Component{
    constructor(props){
        super(props);
        let style = {}
        if(this.props.multiSelect){
            style.userSelect = 'none';
            style.msUserSelect = 'none';
            style.WebkitUserSelect = 'none';
        }
        this.state = {currentId: 0, ids: [], style: style};
        /*
        if(onEdit!=null){this.ctn.querySelectorAll(selector).forEach(function(e){
                e.classList.add("selEditable");
        })}*/
    }
    render(){
        return <div
            style={this.state.style}
            className={this.props.className}
            onClick={
                ()=>{
                    this.select(null, {});
                }}
        >
        {React.Children.map(this.props.children, child => {
            return <SelectorWrapper style={{boxShadow: this.state.ids.includes(child.props.id)?"0px 3px 18px var(--shadowBG)":null, transition:"box-shadow 0.3s", margin: "10px 5px"}} onSelect={(e, keys)=>{this.select(e, keys)}}>{child}</SelectorWrapper>;
        })}
        </div>;
    }
    select(element, keys){
        if(element===null){
            this.setState({currentId: 0, ids: []});
            this.props.selectCallback(null, {currentId: 0, ids: []});
        } else {
            const targetId = element.props.id;
            if(this.props.multiSelect&&keys.shift){
                //multiselect
                let inRange = false;
                let newIds = [];
                React.Children.forEach(this.props.children, child => {
                    if(inRange==false&&(child.props.id===targetId||child.props.id===this.state.currentId)){
                        // start of range
                        inRange = true;
                        newIds.push(child.props.id);
                    } else if(inRange&&(child.props.id!==targetId&&child.props.id!==this.state.currentId)){
                        // in range
                        newIds.push(child.props.id);
                    } else if(inRange){
                        // end of range
                        inRange = false;
                        newIds.push(child.props.id);
                    }
                    this.setState({currentId: targetId, ids: newIds});
                    this.props.selectCallback(element, {currentId: targetId, ids: newIds});
                });
            } else if(this.props.multiSelect&&keys.meta){
                // select/deselect while keeping selection
                let newIds = this.state.ids;
                if(newIds.includes(targetId)){
                    newIds = newIds.filter(itemId => itemId!=targetId);
                } else {newIds.push(targetId)}
                this.setState({currentId: targetId, ids: newIds});
                this.props.selectCallback(element, {currentId: targetId, ids: newIds});
            } else {
                // single select
                this.setState({currentId: targetId, ids: [targetId]});
                this.props.selectCallback(element, {currentId: targetId, ids: [targetId]});
            }
        }
    }
}

function parseHTML(i){
    // parses masked HTML tags and purifies them.
    if(i==null){
        return {__html: null};
    }else{
        return {__html: DOMPurify.sanitize(i.replace(/&lt;/g, "<").replace(/&gt;/g, ">"), { ADD_TAGS: ["aut", "gruen", "gelb", "rot", "blau"] })};
    }
}

export { parseHTML, Status, SelectMenu, Selector, AutoComplete };